body {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  background-color: #0f1424;
  height: 100vh;
  width: 100vw;
  font-size: 18px;
  color: #fff;
  font-family: "Rubik", sans-serif;
}

h1 {
  font-size: 2.2em;
  font-weight: 300;
  line-height: 1.2em;
}
@media screen and (max-width: 800px) {
  h1 {
    font-size: 1.3em;
  }
}

h2 {
  font-size: 0.8em;
  color: rgba(255, 255, 255, 0.6);
  line-height: 1.2em;
}

h3 {
  font-size: 2.5em;
  font-weight: 300;
}
@media screen and (max-width: 800px) {
  h3 {
    font-size: 1.7em;
  }
}

p {
  color: rgba(255, 255, 255, 0.6);
}
p:hover, p.active {
  color: #fff;
}

li {
  color: rgba(255, 255, 255, 0.6);
}
li:hover, li.active {
  color: #fff;
}

.App {
  width: 100vw;
}
@media screen and (max-width: 800px) {
  .App {
    height: 100vh;
  }
}

.main {
  display: flex;
  gap: 25px;
  margin: auto 12%;
}
@media screen and (max-width: 800px) {
  .main {
    flex-wrap: wrap;
    justify-content: center;
    margin: auto 8%;
  }
}
.main .report {
  display: flex;
  flex-direction: column;
  width: 255px;
  background-color: #1c1f4a;
  border-radius: 15px;
  height: 80vh;
}
@media screen and (max-width: 800px) {
  .main .report {
    width: 325px;
    height: 200px;
    margin-top: 20px;
  }
}
.main .report .header {
  height: 70%;
  background-color: #5847eb;
  border-radius: 15px;
  padding: 35px;
  width: 255px;
}
@media screen and (max-width: 800px) {
  .main .report .header {
    display: flex;
    flex-direction: row;
    height: 70%;
    width: 325px;
    padding: 25px;
  }
}
.main .report .header .profil-img {
  border-radius: 40px;
  border: 3px solid #fff;
  width: 80px;
  height: 80px;
}
.main .report .header .profil-name {
  margin-top: 50px;
}
@media screen and (max-width: 800px) {
  .main .report .header .profil-name {
    margin-top: 20px;
    margin-left: 20px;
  }
}
.main .report .content {
  height: 30%;
}
@media screen and (max-width: 800px) {
  .main .report .content {
    width: 325px;
  }
}
.main .report .content ul {
  display: flex;
  flex-direction: column;
  justify-content: space-evenly;
  align-items: flex-start;
  padding: 0 35px 10px 35px;
  height: 100%;
}
@media screen and (max-width: 800px) {
  .main .report .content ul {
    flex-direction: row;
    justify-content: space-evenly;
    align-items: center;
  }
}
.main .report .content ul li {
  cursor: pointer;
}
.main .boxes {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  justify-content: space-between;
}
@media screen and (max-width: 800px) {
  .main .boxes {
    width: 325px;
  }
}
.main .boxes > div {
  border-radius: 15px 15px 50px 50px;
}
.main .boxes .header {
  height: 45px;
  width: 255px;
}
@media screen and (max-width: 800px) {
  .main .boxes .header {
    width: 325px;
    background-position-x: 230px;
  }
}
.main .boxes .content {
  height: 200px;
  width: 255px;
  border-radius: 15px;
  background-color: #1c1f4a;
  padding: 35px;
  margin-bottom: 35px;
}
@media screen and (max-width: 800px) {
  .main .boxes .content {
    width: 325px;
    height: 120px;
  }
}
.main .boxes .content svg {
  fill: #BBC0FF;
  fill-rule: evenodd;
}
.main .boxes .content svg:hover {
  fill: #fff;
  fill-rule: evenodd;
  cursor: pointer;
}
@media screen and (max-width: 800px) {
  .main .boxes .content .content-detail {
    display: flex;
    justify-content: space-between;
  }
}
.main .boxes .work {
  background-color: #ff8c66;
  border-radius: 20px 20px 50px 50px;
}
.main .boxes .work .header {
  background: url("/src/images/icon-work.svg");
  background-size: 79px 79px;
  background-position-x: 160px;
  background-repeat: no-repeat;
  background-position-y: -10px;
}
@media screen and (max-width: 800px) {
  .main .boxes .work .header {
    background-position-x: 230px;
  }
}
.main .boxes .work .content {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}
.main .boxes .work .content .content-title {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  font-weight: 500;
}
@media screen and (max-width: 800px) {
